        ></el-col>
        <el-col :span="10"
          ><div class="grid-content bg-purple">
            {{ item.officePhone }}
          </div></el-col
        >
      </el-row>

      <el-row type="flex" class="row-bg" justify="center">
        <el-col :span="6"
          ><div class="grid-content bg-purple">出生日期</div></el-col
        >
        <el-col :span="2"
          ><div class="grid-content bg-purple-light"></div
        ></el-col>
        <el-col :span="10"
          ><div class="grid-content bg-purple">
            {{ item.hiredate }}
          </div></el-col
        >
      </el-row>

      <el-row type="flex" class="row-bg" justify="center">
        <el-col :span="6"
          ><div class="grid-content bg-purple">部门</div></el-col
        >
        <el-col :span="2"
          ><div class="grid-content bg-purple-light"></div
        ></el-col>
        <el-col :span="10"
          ><div class="grid-content bg-purple">
            {{ item.dept }}
          </div></el-col
        >
      </el-row>

      <el-row type="flex" class="row-bg" justify="center">
        <el-col :span="6"
          ><div class="grid-content bg-purple">职位</div></el-col
        >
        <el-col :span="2"
          ><div class="grid-content bg-purple-light"></div
        ></el-col>
        <el-col :span="10"
          ><div class="grid-content bg-purple">
            {{ item.job }}
          </div></el-col
        >
      </el-row>

      <el-row type="flex" class="row-bg" justify="center">
        <el-col :span="6"
          ><div class="grid-content bg-purple">邮箱</div></el-col
        >
        <el-col :span="2"
          ><div class="grid-content bg-purple-light"></div
        ></el-col>
        <el-col :span="10"
          ><div class="grid-content bg-purple">
            {{ item.email }}
          </div></el-col
        >
      </el-row>
    </div>
    <el-col :span="6"><div class="grid-content bg-purple"></div></el-col>
    <el-col :span="6"
      ><div class="grid-content bg-purple">
        <el-button type="primary" @click="edit">编辑</el-button>
      </div></el-col
    >
    <el-col :span="6"
      ><div class="grid-content bg-purple">
        <el-button type="danger" @click="deleteUser">删除</el-button>
      </div></el-col
    >
    <el-col :span="6"><div class="grid-content bg-purple"></div></el-col>
  </div>
</template>

<script>
import Title from "../nav/title.vue";
export default {
  name: "",
  components: {
    Title
  },
  data() {
    return {
      circleUrl:
        "https://cube.elemecdn.com/3/7c/3ea6beec64369c2642b92c6726f1epng.png",
      user: {}
    };
  },
  mounted() {
    this.getList();
  },
  methods: {
    getList() {
      console.log(this.$route.params.id);
      this.axios
        .get(this.baseUrl + "client/queryClientId", {
          params: { id: this.$route.query.id }
        })
        .then(res => {
          this.user = res.data;
        });
    },
    submitUser() {
      this.axios
        .post(
          this.baseUrl + "client/addClient",
          this.$qs.stringify(this.user, {
            arrayFormat: "indices",
            allowDots: true
          })
        )
        .then(res => {
          if (res.data.code == 200) {
            this.$message({
              message: res.data.message,
              type: "success"
            });
            this.$router.push({ name: "localUser" });
          } else {
            this.$message({
              message: res.data.message,
              type: "error"
            });
          }
        });
    },
    edit() {
      console.log(this.user.id);
      this.$router.push({ name: "localEdit", query: { id: this.user.id } });
    },
    deleteUser() {
      this.axios
        .delete(this.baseUrl + "client/delete", {
          params: { id: this.user.id }
        })
        .then(res => {
          if (res.data.code == 200) {
            this.$message({
              message: res.data.message,
              type: "success"
            });
            setTimeout(() => {
              this.$router.push({ name: "localUser" });
            }, 2000);
          } else {
            this.$message({
              message: res.data.message,
              type: "error"
            });
          }
        });
    }
  }
};
</script>

<style scoped>
.el-row {
  font-size: 14px;
  margin-bottom: 8px;
}
.el-col {
  border-radius: 4px;
}

.grid-content {
  border-radius: 4px;
  min-height: 36px;
}
</style>



